import axios from 'axios';
import {AuthRefreshResponse, AuthResponse} from "../models/response/AuthResponse";

export const SERVER_URL = `https://a4c4-94-126-201-166.ngrok-free.app`
export const API_URL = `${SERVER_URL}/api/v1`

const $api = axios.create({
  withCredentials: true,
  baseURL: API_URL
})

$api.interceptors.request.use((config) => {
  config.headers.Authorization = `Bearer ${localStorage.getItem('token')}`
  return config
})

$api.interceptors.response.use((config) => {
  return config;
}, async (error) => {
  const originalRequest = error.config;
  if(error.response.status == 401 && error.config && !error.config._isRetry) {
    console.log('error', error)
    originalRequest._isRetry = true;
    try{
      const response = await axios.post<AuthRefreshResponse>(`${API_URL}/refresh/`, {refresh: localStorage.getItem('refreshToken')},{withCredentials: true})
      localStorage.setItem('token', response.data.access)
      return $api.request(originalRequest);
    } catch (e) {
      console.log(' ')
    }
  }
  throw error;
})

export default $api;