import React, {useState} from 'react';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';

const Publication = ({post}) => {
      const [showFullText, setShowFullText] = useState(false);

      const toggleTextVisibility = () => {
        setShowFullText(prevState => !prevState);
      };

      return (
          <div
              style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'flex-start',
                background: '#FFF',
                width: '100%',
                padding: 20,
                borderRadius: 15,
                gap: 10
              }}
          >
            <div style={{display: 'flex', gap: 10, alignItems: 'center'}}>
              <AccountCircleIcon style={{color: '#30434A', fontSize: 32}}/>
              <span style={{fontSize: 18}}>{post.user ?? 'Name Surname'}</span>
            </div>

            {post.description.length <= 325 ?
                <div style={{textAlign: 'left'}}>
                  {post.description}
                </div>
                :

                showFullText ?
                    <div onClick={toggleTextVisibility} style={{textAlign: 'left'}}>
                      {post.description}
                    </div>
                    :
                    <div
                        style={{
                          overflow: 'hidden',
                          textOverflow: 'ellipsis',
                          display: '-webkit-box',
                          WebkitLineClamp: 2,
                          WebkitBoxOrient: 'vertical',
                          cursor: 'pointer',
                          textAlign: 'left'
                        }}
                        onClick={toggleTextVisibility}
                    >
                      {`${post.description.slice(0, 325)}...`}
                    </div>
            }

            {post.description.length > 325 &&
                < span style={{color: '#B0B0B0', cursor: 'pointer'}} onClick={toggleTextVisibility}>
                   {showFullText ? 'hide' : 'See more...'}
                </span>
            }

            <img src={post.image} alt='' style={{width: '100%'}}/>
          </div>
      )
    }

export default Publication;
