import {AxiosResponse} from "axios";
import {Category, DefInfo, EditProfileInfoResponse} from "../models/profileEdit/ProfileEditResponse";
import $api from "../http";

export default class ProfileEditService {
  static async editProfileInfo(
    id: number,
    user_name: string,
    first_name: string,
    last_name: string,
    about_me: string,
    work_place: string
  ): Promise<AxiosResponse<EditProfileInfoResponse>> {
    return $api.patch(`users/${id}/`,{user_name, first_name, last_name, about_me, work_place})
  }

  static async getCategories(): Promise<AxiosResponse<Category[]>>{
    return $api.get('category/')
  }

  static async getProfileDefaultInfo(id: number): Promise<AxiosResponse<DefInfo>>{
    return $api.get(`users/${id}/`)
  }
}