import {AxiosResponse} from "axios";
import {GetAvatar, GetProfileInfoResponse, GetRoles} from "../models/profile/ProfileResponse";
import $api from "../http";

export default class ProfileService {
  static async getUserInfo(id: number): Promise<AxiosResponse<GetProfileInfoResponse>> {
    return $api.get(`/users/${id}/`)
  }

  static async getUserAvatar(id: number): Promise<AxiosResponse<GetAvatar>>{
    return $api.get(`/avatar/?user=${id}`)
  }

  static async getRoles(): Promise<AxiosResponse<GetRoles>>{
    return $api.get('/roles/')
  }
}