import {AxiosResponse} from 'axios'
import $api from "../http";
import {AuthResponse} from "../models/response/AuthResponse";

export default class AuthService {
    static async login(username: string, password: string): Promise<AxiosResponse<AuthResponse>> {
        return $api.post('/login/', {user_name: username, password})
    }

    static async register(username: string, password: string, password2: string, email: string): Promise<AxiosResponse<AuthResponse>> {
        return $api.post('/register/', {user_name: username, password, password2, email})
    }
}