import React, {useContext, useState} from 'react';
import {useHistory} from 'react-router-dom';
// @ts-ignore
import styles from './style.module.css'
import {Context} from "../../index";
import CustomInput from "../../common/components/customInput/CustomInput";
import LogoSvgLogin from "../../assets/icons/LogoSciFiLogin/LogoSvgLogin";
import CustomButton from "../../common/components/customButton/CustomButton";
import EyeShow from "../../assets/icons/eye-show";
import EyeOff from "../../assets/icons/eye-off";
// @ts-ignore
import googleIcon from '../../assets/img/google-icon.png'
// @ts-ignore
import fbIon from "../../assets/img/fb-icon.png"

const Login = () => {
  const history = useHistory();

  const [username, setUsername] = useState<string>('')
  const [password, setPassword] = useState<string>('')
  const [showPassword, setShowPassword] = useState<boolean>(false)
  const {store} = useContext(Context)


  const navigateToHome = () => {
    history.push('/signUp');
  };

  const doLogin = () => {
    store.login(username, password)
      .then(res => {
        setTimeout(() => {
          // @ts-ignore
          store.isAuth && localStorage.getItem('me') && history.push(`/profile/${username}`)
        }, 300)
      })
  }

  return (
    <div className={styles.loginWrapper}>
      <div className={styles.logo}>
        <LogoSvgLogin/>
      </div>

      <div className={styles.centerBlock}>
        <div className={styles.formLogin}>
          <h2>Log in</h2>
          <CustomInput
            value={username}
            onChange={e => setUsername(e.target.value)}
            type='text'
            placeholder='Username or Email address'
          />
          <CustomInput
            value={password}
            onChange={e => setPassword(e.target.value)}
            type={showPassword ? 'text' : 'password'}
            placeholder='Password'
            icon={showPassword ? <EyeOff/> : <EyeShow/>}
            onIconClick={() => {setShowPassword(!showPassword)}}
          />

          <CustomButton
            title='Log in'
            onClick={doLogin}
            style={{width: '100%'}}
          />

        </div>

        <div className={styles.createAcc}>
          <span>don't have an account?</span> <span className='link' onClick={navigateToHome}>Create it!</span>
        </div>

        <div className={styles.orLineContainer}>
          <div className={styles.orLine}></div>
          <div> OR </div>
          <div className={styles.orLine}></div>
        </div>

        <div className={styles.smContainer}>
          <div className={styles.socialMediaBtn}>
            <img src={googleIcon} alt='' width={32}/>
            Continue with google
          </div>
          <div className={styles.socialMediaBtn}>
            <img src={fbIon} alt='' width={32}/>
            Continue with facebook
          </div>
        </div>

      </div>


    </div>
  );
};

export default Login;