import {makeAutoObservable} from "mobx";
import ProfileService from "../services/ProfileService";

interface Role {
  id: number;
  role_key: string;
}

export default class ProfileStore {

  userInfo = {}
  avatar = null
  roles: Role[] = [];

  constructor() {
    makeAutoObservable(this);
  }

  setUserData = (data: object) => {
    this.userInfo = data
  }

  setAvatar = (data: any) => {
    this.avatar = data?.user_avatar_url
  }

  setRoles = (data: any) => {
    this.roles = data
  }

  async getUserInfo(id: number) {
    try{
      const response = await ProfileService.getUserInfo(id)
      this.setUserData(response.data)
    } catch (e: any) {
      console.log(e.response?.data?.message);
    }
  }

  async getUserAvatar(id: number) {
    try {
      const response = await ProfileService.getUserAvatar(id)
      this.setAvatar(response.data)
    } catch (e: any) {
      console.log(e.response?.data?.message);
    }
  }

  async getRoles() {
    try {
      const response = await ProfileService.getRoles()
      this.setRoles(response.data)
    } catch (e: any) {
      console.log(e.response?.data?.message);
    }
  }
}