import React from 'react';
import CustomGrid from "../../common/components/CustomGrid";
import HeaderTitle from "../../common/components/HeaderTitle";
import UploadAvatar from "./components/UploadAvatar";
import EditProfileForm from "./components/EditProfileForm";
import {observer} from "mobx-react-lite";
import ProfileEditStore from "../../store/profileEdit";
import {FormEdited} from "../../models/profileEdit/ProfileEditResponse";
import useCustomNavigation from "../../common/hooks/useCustomNavigation";
import InterestsSelect from "./components/EditProfileForm/InterestsSelect";

const EditProfile = observer(() => {
  const profileEditStore = new ProfileEditStore()
  const { navigateTo } = useCustomNavigation();

  const submitForm = (form: FormEdited) => {
    profileEditStore.edit(form.id, form.user_name, form.first_name, form.last_name, form.about_me, form.work_place)
      .then(res => {
        navigateTo(`/profile/${form.user_name}`)
      })
      .catch( e => {
        console.log(e)
      })
  }
  return (
    <CustomGrid >
      <HeaderTitle title='Edit profile' size={20}/>
      <UploadAvatar/>
      <div style={{display: "flex", justifyContent: 'flex-start', gap: '20%', width: '100%'}}>
        <div style={{width: '25%'}}>
          <EditProfileForm onSubmit={submitForm}/>
        </div>

        <div style={{width: '25%'}}>
          <HeaderTitle title='Choose the topics you are interested in' size={20}/>
          <InterestsSelect/>
        </div>

      </div>

    </CustomGrid>
  )
});
export default EditProfile;