import {makeAutoObservable} from "mobx";
import AuthService from "../services/AuthService";
import axios from "axios";
import {AuthRefreshResponse, AuthResponse} from "../models/response/AuthResponse";
import $api, {API_URL} from "../http";

export default class Store {
  isAuth = false;
  user_id = 0;
  isLoading = false;


  constructor() {
    makeAutoObservable(this);
  }

  setAuth(bool: boolean) {
    this.isAuth = bool;
  }

  setLoading(bool: boolean) {
    this.isLoading = bool;
  }

  setUserId(id: number){
    this.user_id = id
  }


  async login(username: string, password: string) {
    try {
      const response = await AuthService.login(username, password);
      localStorage.setItem('token', response.data.access)
      localStorage.setItem('refreshToken', response.data.refresh)
      this.setAuth(true);
      $api.get('/me').then(res => {
        localStorage.setItem('me', JSON.stringify(res.data))
        this.setUserId(res.data.id)
      })
    } catch (e: any) {
      console.log(e.response?.data?.message);
    }
  }

  async registration(username: string, password: string, password2: string, email: string) {
    try {
      // const response = await AuthService.register(username, password, password2, email);
      const response = await axios.post(`${API_URL}/register/`, {user_name: username, password, password2, email});
      localStorage.setItem('refreshToken', response.data.refresh)
      localStorage.setItem('token', response.data.access)
      this.setAuth(true);
      $api.get('/me').then(res => {
        localStorage.setItem('me', JSON.stringify(res.data))
        this.setUserId(res.data.id)
      })
    } catch (e: any) {
      console.log(e.response?.data?.message);
    }
  }

  async logout() {
    try {
      localStorage.removeItem('token')
      localStorage.removeItem('refreshToken')
      localStorage.removeItem('me')
      this.setAuth(false);
    } catch (e: any) {
      console.log(e.response?.data?.message);
    }
  }

  async checkAuth() {
    this.setLoading(true);
    try {
      const response = await axios.post<AuthRefreshResponse>(`${API_URL}/refresh/`, {refresh: localStorage.getItem('refreshToken')},{withCredentials: true})
      localStorage.setItem('token', response.data.access)
      this.setAuth(true);
    } catch (e: any) {
      console.log(e.response?.data?.message);
    } finally {
      this.setLoading(false)
    }
  }

}