import React, {useContext} from 'react';
// @ts-ignore
import styles from './style.module.css'
import LogoSvgLogin from "../../../assets/icons/LogoSciFiLogin/LogoSvgLogin";
import NewsIcon from "../../../assets/icons/news-icon";
import ChatIcon from "../../../assets/icons/chat-icon";
import {SERVER_URL} from "../../../http";
// @ts-ignore
import profileImg from '../../../assets/img/profile icon.png'
import SettingsIcon from "../../../assets/icons/settings-icon";
import {Context} from "../../../index";
import {useHistory} from "react-router-dom";
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LogoutIcon from '@mui/icons-material/Logout';

const HeaderDesktop = () => {
  const {store} = useContext(Context);
  const history = useHistory();

  const handleNavigate = (path: string) => {
    history.push(path)
  }

  const meString = localStorage.getItem('me');
  const me = meString ? JSON.parse(meString) : null;
  const myId = me ? me.id : 1

  const menu = [
    {
      title: 'News',
      path: '/news',
      icon: <NewsIcon/>
    },
    {
      title: 'Messaging',
      path: '/messaging',
      icon: <ChatIcon/>
    },
    {
      title: 'My profile',
      path: `/profile/${myId}`,
      // icon:  <img src={profileStore.avatar ? SERVER_URL + profileStore.avatar : profileImg} alt="" style={{borderRadius: '50%'}} className={styles.icon} />
      icon:  <AccountCircleIcon style={{color: 'white', fontSize: 32}}/>
    },
    {
      title: 'Settings',
      path: '/settings',
      icon: <SettingsIcon/>
    }
  ]

  const handleLogout = () => {
    store.logout().then(res => {
      window.location.href = '/';
    });
  };

  return (
    <div className={styles.headerWrapper}>
      <div className={styles.logo}>
        <LogoSvgLogin/>
      </div>

      <div className={styles.menu}>
        {menu.map(item =>
          <div className={styles.menuItem} onClick={() => handleNavigate(item.path)}>
            <div className={styles.icon}>{item.icon}</div>
            <span>{item.title}</span>
          </div>
        )}
        <div className={styles.menuItem} onClick={handleLogout}>
          <div className={styles.icon}><LogoutIcon fontSize='inherit' color='inherit'/></div>
          <span>Log out</span>
        </div>
      </div>

    </div>
  );
};

export default HeaderDesktop;