import ProfileEditService from "../services/ProfileEditService";
import {makeAutoObservable} from "mobx";
import {AxiosResponse} from "axios";
import {Category, DefInfo} from "../models/profileEdit/ProfileEditResponse";
import $api from "../http";

export default class ProfileEditStore {

  interests: Category[] = [];
  userDefaultInfo: {} | undefined

  constructor() {
    makeAutoObservable(this)
  }

  setInterests = (data: Category[]) => {
    this.interests = data
  }

  setUserInfo = (data: DefInfo) => {
    this.userDefaultInfo = data
  }

  async edit(id: number,
             user_name: string,
             first_name: string,
             last_name: string,
             about_me: string,
             work_place: string) {
    try {
      const res = await ProfileEditService.editProfileInfo(id, user_name, first_name, last_name, about_me, work_place)
    } catch (e: any){
      console.log(e.response?.data?.message)
    }
  }

  async getInterests() {
    try {
      const res = await ProfileEditService.getCategories()
      this.setInterests(res.data)
      return res.data
    } catch (e: any){
      console.log(e.response?.data?.message)
    }
  }

  async getDefUserInfo(id: number) {
   try {
     const res = await ProfileEditService.getProfileDefaultInfo(id)
     this.setUserInfo(res.data)
     return res.data
   } catch (e: any){
     console.log(e.response?.data?.message)
   }
  }

}