import React from 'react';
// @ts-ignore
import styles from './style.module.css'
// @ts-ignore
import testAva from '../../../../assets/img/avaProfTest.png';
import CustomButton from "../../../../common/components/customButton/CustomButton";
import useCustomNavigation from "../../../../common/hooks/useCustomNavigation";

interface ProfileInfoProps {
  avatar: string;
  info: any,
  roleTitle: string
}
const AvatarBox: React.FC<ProfileInfoProps> = ({info, avatar, roleTitle}) => {
  const { navigateTo } = useCustomNavigation();

  return (
    <div className={styles.avatarBoxWrapper}>
      <div className={styles.avablock}>
        <img src={testAva} className={styles.avaImg}/>
        <div className={styles.avaText}>
          <span>
            {info?.first_name?.trim() ? info?.first_name : 'Name '}
            {info?.last_name?.trim() ? info?.last_name : 'Surname'}
          </span>
          <span>{roleTitle ?? 'Scientist'}</span>
          <CustomButton title='Edit profile' onClick={() => {navigateTo('/editProfile')}}/>
        </div>
      </div>
    </div>
  );
};

export default AvatarBox;