import React, { ChangeEvent, CSSProperties, ReactNode, MouseEvent } from 'react';
// @ts-ignore
import styles from './style.module.css';

interface CustomInputProps {
  type?: string;
  value: string;
  placeholder?: string;
  onChange: (e: ChangeEvent<HTMLInputElement>) => void;
  style?: CSSProperties;
  icon?: ReactNode; //   
  onIconClick?: (e: MouseEvent<HTMLSpanElement>) => void; //      
  label?: string;
  errorText?: string
}

const CustomInput: React.FC<CustomInputProps> = ({
                                                   type = 'text',
                                                   value,
                                                   placeholder,
                                                   onChange,
                                                   style,
                                                   icon,
                                                   onIconClick,
                                                   label,
                                                   errorText
                                                 }) => {
  return (
    <div
      style={{
        width: '100%',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start'
      }}
    >
      {label &&
          <span className={styles.label}>{label}</span>
      }

      <div
        className={styles.customInputWrapper}
        style={{
          width: '100%',
          padding: '0',
          margin: '5px 0',
          boxSizing: 'border-box',
          display: 'flex',
          alignItems: 'center',
          justifyContent: "space-between",
          ...style,
        }}
      >
        <input
          className={styles.customInput}
          type={type}
          value={value}
          placeholder={placeholder}
          onChange={onChange}
        />

        {icon && (
          <span
            onClick={onIconClick}
            style={{
              cursor: onIconClick ? 'pointer' : 'default',
              display: 'flex',
              alignItems: "center",
              marginRight: '10px',
              justifyContent: 'flex-end',
            }}
          >
          {icon}
        </span>
        )}
      </div>
      {errorText &&
          <span className={styles.errorText}>{errorText}</span>
      }

    </div>

  );
};

export default CustomInput;
