import React from 'react';

interface HeaderTitleProps{
  title: string,
  size?: number,
  style?: any,
}
const HeaderTitle: React.FC<HeaderTitleProps> = ({title, size, style}) => {
  return (
    <span
      style={{
        color: '#000',
        fontSize: size ?? 30,
        fontStyle: 'normal',
        lineHeight: 'normal',
        ...style
      }}
    >
      {title}
    </span>
  );
};

export default HeaderTitle;