import React, { useState } from 'react';
import Stack from "@mui/material/Stack";
import Chip from "@mui/material/Chip";
import HeaderTitle from "../../../../../common/components/HeaderTitle";
import { styled } from "@mui/material";

const Wrapper = styled('div')({
  display: 'flex',
  flexDirection: 'column',
  gap: 10,
  padding: 10,
});

const ChipContainer = styled(Stack)({
  flexWrap: 'wrap',
  justifyContent: 'center',
  // width: '50%',
  alignSelf: "center",

  '& .css-1r3mz6a-MuiButtonBase-root-MuiChip-root ': {
    color: '#386A7C',
    border: '1px solid #386A7C'
  }
});

const ChipContainerMy = styled(Stack)({
  flexWrap: 'wrap',
  justifyContent: 'center',
  // width: '50%',
  alignSelf: "center",

  '& .css-38raov-MuiButtonBase-root-MuiChip-root ': {
    background: '#386A7C',
    color: 'white'
  }
});

const InterestsSelect = () => {
  const [options, setOptions] = useState([
    {
      title: 'medicine',
      value: 0,
    },
    {
      title: 'chemistry',
      value: 1,
    },
    {
      title: 'biology',
      value: 3,
    },
    {
      title: 'physics',
      value: 4,
    },
    {
      title: 'mathematics',
      value: 5,
    },
    {
      title: 'engineering',
      value: 6,
    },
    {
      title: 'computer science',
      value: 7,
    },
    {
      title: 'astronomy',
      value: 8,
    },
    {
      title: 'geology',
      value: 9,
    },
    {
      title: 'psychology',
      value: 10,
    },
  ]);

  const [myInterests, setMyInterests] = useState([
    {
      title: 'economics',
      value: 11,
    },
    {
      title: 'sociology',
      value: 12,
    },
  ]);

  const handleClick = (chip, idx) => {
    setMyInterests((prevInterests) => [...prevInterests, chip]);
    setOptions((prevOptions) => prevOptions.filter((_, i) => i !== idx));
  };

  const handleDelete = (chip, idx) => {
    setMyInterests((prevInterests) => prevInterests.filter((_, i) => i !== idx))
    setOptions(prevOptions => [...prevOptions, chip])
  }

  return (
      <Wrapper>
        <ChipContainer direction="row" spacing={1}>
          {options.map((op, idx) => (
              <Chip
                  variant='outlined'
                  key={idx}
                  label={op.title}
                  onClick={() => handleClick(op, idx)}
              />
          ))}
        </ChipContainer>

        <HeaderTitle title='I am interested in:' size={19} style={{ marginTop: 10 }} />
        <ChipContainerMy direction="row" spacing={1}>
          {myInterests.map((it, idx) => (
              <Chip
                  key={idx}
                  label={it.title}
                  onDelete={() => handleDelete(it, idx)}
              />
          ))}
        </ChipContainerMy>
      </Wrapper>
  );
};

export default InterestsSelect;
