import React, {useContext, useEffect, useState} from 'react';
import { useParams } from 'react-router-dom';
import {observer} from "mobx-react-lite";
import { toJS } from 'mobx';
import {SERVER_URL} from "../../http";
// @ts-ignore
import styles from './style.module.css'
import AvatarBox from "./components/AvatarBox/AvatarBox";
import ProfileDescriptionBox from "./components/ProfileDescriptionBox/ProfileDescriptionBox";
import BasicTabs from "../../common/components/BasicTabs";
import {Context} from "../../index";
import ProfilePublications from "./components/ProfilePublications";


interface RouteParams {
  username: string;
}

interface ProfileInfo {
  avatar: string;
  info: {
    user_name: string;
    first_name: string;
    last_name: string;
    id: number;
    email: string;
    interests?: number | null;
    role: number;
    work_place: string;
  }
}

const Profile = observer(() => {
  const { username } = useParams<RouteParams>();
  const {profileStore} = useContext(Context)
  const [profileInfo, setProfileInfo] = useState<ProfileInfo>()
  const [myRole, setMyRole] = useState('')

  const [me, setMe] = useState(() => {
    const storedMe = localStorage.getItem('me');
    return storedMe ? JSON.parse(storedMe) : null;
  });

  const fetchData = async () => {
    await profileStore.getUserInfo(me.id);
    console.log('profileStore', toJS(profileStore.userInfo));
    setProfileInfo((prev: any) => ({ ...prev, info: toJS(profileStore.userInfo) }));
    await profileStore.getUserAvatar(me.id)
    setProfileInfo((prev: any) => ({ ...prev, avatar: SERVER_URL + profileStore.avatar}))
  };

  useEffect(() => {
    profileStore.getRoles()
    if (me && me.id) {
      fetchData();
    }
  }, []);

  useEffect(() => {
    const myRoleTemp = profileInfo?.info?.role ? getRoleKeyById(profileInfo?.info?.role) : ''
    // @ts-ignore
    setMyRole(myRoleTemp)
  }, [profileInfo]);

  function getRoleKeyById(id: number) {
    const role = profileStore.roles.find(role => role.id === id);
    return role ? role.role_key : null;
  }


  const tabsConfig = [
    { label: 'About Scientist', component: <ProfileDescriptionBox info={profileInfo?.info} /> },
    { label: 'Publications', component: <ProfilePublications /> },
  ];

  return (
      <div className={styles.ProfileWrapper}>
        <div style={{display: "flex", gap: 30, width: '100%', justifyContent: "center"}}>
          <AvatarBox info={profileInfo?.info} avatar={profileInfo?.avatar ?? ''} roleTitle={myRole}/>
          <div style={{width: '75%'}}>
            <BasicTabs tabs={tabsConfig}/>
          </div>
        </div>
      </div>
  );
});

export default Profile;